import { NextRequest } from 'next/server'
import payload from 'payload'

// Payload singleton
let payloadInstance: any = null

async function getPayload() {
  if (!payloadInstance) {
    // Import config - path from src/app/admin to root is ../../..
    const config = await import('../../../../payload.config.ts')
    
    // Initialize Payload
    payloadInstance = await payload.init({
      ...config.default,
      local: true,
    })
  }
  return payloadInstance
}

// Next.js App Router handlers for Payload Admin
export async function GET(req: NextRequest) {
  try {
    const pl = await getPayload()
    // @ts-ignore
    return await pl.handler(req)
  } catch (error: any) {
    console.error('Payload GET error:', error)
    return new Response(JSON.stringify({ error: error.message }), {
      status: 500,
      headers: { 'Content-Type': 'application/json' },
    })
  }
}

